</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { reactive, computed, onMounted, ref  } from 'vue'
	import Swal from 'sweetalert2'
	import axios from 'axios'
	import store from '@/store'
	import router from '@/router'
	import tambah_nol from '@/tambah_nol'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])
			const loading = ref(false)

		    // Generate Kode Tindakan
			const kode_cabang = ref('')
			const kode_dari_nama = ref('')
			const nomor_urut = ref('')
			const cek = ref(0)

		    const generateRekamMedis = (nama) => {
		    	axios.get(`api/cabang/getWhereId/${user.value.cabang_id}`)
		    	.then((response) => {
		    		kode_cabang.value = `${response.data.kode_cabang}`
		    	}) 
		    	.catch((error) => {
		    		console.log(error)
		    	})
		    	
		    	if (nama != undefined) {
		    		kode_dari_nama.value = nama.slice(0, 1)
		    	}
		    	
		    	if (kode_cabang.value.length > 0 && kode_dari_nama.value != '') {
			    	axios.get(`api/pasien/getForGenerateCode/${kode_cabang.value}-${kode_dari_nama.value}`)
			    	.then((response) => {
			    		console.log(response.data)
			    		if(response.data == 'kosong'){
			    			cek.value = 1
			    			nomor_urut.value = tambah_nol(1)
			    		} else {
			    			cek.value = 1
			    			let str = response.data
							let length = str.length
							let nlength = length - 3
							let id = str.slice(nlength, length) 
			    			id = Math.abs(id)
			    			nomor_urut.value = tambah_nol(id + 1)
			    		}
			    	})
			    	.catch((error) => {
			    		console.log(error)
			    	})
		    	}

		    }

		    const kode_baru = computed(() => `${kode_cabang.value}-${kode_dari_nama.value}${nomor_urut.value}`)
		    // End

			const pasien = reactive({
				"cabang_id": user.value.cabang_id,
				"nomor_rekam_medis": kode_baru,
				"nama": "",
				"nomor_identitas": "",
				"provinsi": "",
				"kota": "",
				"kecamatan": "",
				"desa": "",
				"alamat_lengkap": "",
				"nomor_telepon": "",
				"jenis_kelamin": "",
				"golongan_darah": "",
				"tempat_lahir": "",
				"tanggal_lahir": "",
				"agama": "",
				"pekerjaan": "",
				"pendidikan": "",
				"sumber_informasi": "",
				"sosial_media": "",
				"nomor_member": "",
				"label": "",
				"foto": "",
				"status_member": false
			})

			const resetDataPasien = () => {
				pasien.nama =  '',
				pasien.nomor_identitas = '',
				pasien.alamat_lengkap = '',
				pasien.nomor_telepon = '',
				pasien.jenis_kelamin = '',
				pasien.golongan_darah = '',
				pasien.tempat_lahir = '',
				pasien.tanggal_lahir = '',
				pasien.agama = '',
				pasien.pekerjaan = '',
				pasien.pendidikan = '',
				pasien.sumber_informasi = '',
				pasien.sosial_media = '',
				pasien.nomor_member = '',
				pasien.label = '',
				pasien.foto = '',
				pasien.status_member = false
			}

			const prosesPasien = () => {
				loading.value = true
				axios.post('api/pasien/create', pasien)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil menambahkan data',
					  icon: 'success',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
					resetDataPasien()
					generateRekamMedis()
					router.push('/resepsionis')
					console.log(response)
				})
				.catch((error) => {
					Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal menambahkan data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
					console.log(error)
				})

				// router.replace('/data-tindakan')
		    }

		    // Get Provinsi
		    const provinsi = ref([])

		    const getProvinsi = async () => {
		    	let response = await axios.get('api/wilayah/provinsi')

		    	provinsi.value = response.data
		    }

		    // End Get Provinsi

		    // Get Kota
		    const kota = ref([])

		    const getKota = async (kode) => {
		    	let response = await axios.get(`api/wilayah/kota/${kode}`)
		    	kota.value = response.data
		    }
		    // End Get Kota

		    // Get kecamatan
		    const kecamatan = ref([])

		    const getKecamatan = async (kode) => {
		    	let response = await axios.get(`api/wilayah/kecamatan/${kode}`)
		    	kecamatan.value = response.data
		    }
		    // End Get kecamatan

		    // Get desa
		    const desa = ref([])

		    const getDesa = async () => {
		    	let response = await axios.get(`api/wilayah/desa/${pasien.kecamatan}`)
		    	desa.value = response.data
		    }
		    // End Get desa


		    onMounted(() => {		    	
		    	getProvinsi()
		    })

			return { pasien, prosesPasien, generateRekamMedis, provinsi, getProvinsi, kota, getKota, kecamatan, getKecamatan, desa, getDesa, resetDataPasien, loading }
		}
	}
</script>

<style>
	.hijau{
		background-color: #10B981;
	}

	.kuning{
		background-color: #FCD34D;
	}

	.merah{
		background-color: #EF4444;
	}

	.hitam{
		background-color: #1F2937;
	}

	.ungu{
		background-color: #8B5CF6;
	}

	.biru{
		background-color: #3B82F6;
	}


</style>